'use client';

import { PageHeader } from "@/components/page-header";
import { AppLayoutClient } from "../app-layout-client";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useSearchParams } from 'next/navigation';

// Placeholder components for each settings tab
const CompanySettings = () => (
  <Card>
    <CardHeader>
      <CardTitle>Empresa</CardTitle>
      <CardDescription>Gerencie os detalhes da sua empresa.</CardDescription>
    </CardHeader>
    <CardContent>
      <p>Conteúdo das configurações da empresa aqui.</p>
    </CardContent>
  </Card>
);

const PlanSettings = () => (
  <Card>
    <CardHeader>
      <CardTitle>Planos</CardTitle>
      <CardDescription>Informações sobre seu plano atual.</CardDescription>
    </CardHeader>
    <CardContent>
      <p>Conteúdo dos detalhes do plano aqui.</p>
    </CardContent>
  </Card>
);

const BillingSettings = () => (
  <Card>
    <CardHeader>
      <CardTitle>Pagamentos</CardTitle>
      <CardDescription>Histórico e gerenciamento de pagamentos.</CardDescription>
    </CardHeader>
    <CardContent>
      <p>Conteúdo de pagamentos aqui.</p>
    </CardContent>
  </Card>
);

const LGPDSettings = () => (
  <Card>
    <CardHeader>
      <CardTitle>LGPD</CardTitle>
      <CardDescription>Configurações de consentimento e privacidade.</CardDescription>
    </CardHeader>
    <CardContent>
      <p>Conteúdo da LGPD aqui.</p>
    </CardContent>
  </Card>
);

const IntegrationsSettings = () => (
    <Card>
      <CardHeader>
        <CardTitle>Integrações</CardTitle>
        <CardDescription>Gerencie suas integrações com outros serviços.</CardDescription>
      </CardHeader>
      <CardContent>
        <p>Conteúdo de integrações aqui.</p>
      </CardContent>
    </Card>
  );

  const renderContent = (tab: string | null) => {
    switch (tab) {
      case 'plan':
        return <PlanSettings />;
      case 'billing':
        return <BillingSettings />;
      case 'lgpd':
        return <LGPDSettings />;
      case 'integrations':
        return <IntegrationsSettings />;
      case 'company':
      default:
        return <CompanySettings />;
    }
  };

export default function SettingsPage() {
    const searchParams = useSearchParams();
    const currentTab = searchParams.get('tab');

  return (
    <AppLayoutClient>
      <PageHeader title="Configurações" />
      <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
        <div className="col-span-1">
          {/* This is where the navigation would be, assuming it's in AppLayoutClient or similar */}
        </div>
        <div className="col-span-1 md:col-span-3">
          {renderContent(currentTab)}
        </div>
      </div>
    </AppLayoutClient>
  );
}